import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import AnimatedProductPromoAdsDescription from "../../../components/animated_product_promo_ads/AnimatedProductPromoAdsDescription/AnimatedProductPromoAdsDescription";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import AnimatedProductBannerTwo from "../../../components/animated_product_promo_ads/AnimatedProductBannerTwo/AnimatedProductBannerTwo";

import Pricing2 from "../../../components/services_page_components/pricing/Pricing2";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"

import { useLocation } from "react-router-dom"

import banner from "./AnimatedProductPromoAds.jpg"

export default function AnimatedProductPromoAds() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="animated-promotion-promo-ads">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Animated Product Promo Ads"
        bannerParagraph="Discover the power of your product through our captivating animated promo ad."
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={banner}
      />

      <AnimatedProductPromoAdsDescription
        headingOne="Our innovative and creatively animated product promotion commercials
        transport the audience and leave a lasting impression. Each
        advertisement is made to reflect your company's personality and goals,
        from clean and contemporary layouts to more lighthearted animations."
      />

      <SubCategoriesCardLeft
        paragrapgh="We use our extensive animation experience to emphasize the most
        important aspects of your items and to show off their usefulness and
        worth. Our animated promo ads are a great way to capture attention,
        educate consumers, and ultimately improve sales when introducing a new
        product, hosting a promotion, or just building brand recognition."
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        heading="Animated Videos"
        paragraph="Eye-catching images, vivid colors, seamless transitions, and fluid movements all come together in our animated videos. We take great effort in designing each image to best illustrate your story. We can bring any idea to life, whether it's in the form of a 2D or 3D animation, a motion graphic, or a character animation."
        button="LET'S CHAT"
        picture={pic2}
      />

      <SubCategoriesCardLeft
        heading="Product Promo Videos"
        paragrapgh="Our innovative and creatively animated product promotion commercials transport the audience and leave a lasting impression. Each advertisement is made to reflect your company's personality and goals, from clean and contemporary layouts to more lighthearted animations."
        button="LET'S CHAT"
        picture={pic3}
      />

      <AnimatedProductPromoAdsDescription headingOne="We create animated promo ads which are a great way to capture attention, educate consumers, and ultimately improve sales when introducing a new product, hosting a promotion, or just building brand recognition." />

      <AnimatedProductBannerTwo/>

      <br/>
      <br/>
      <br/>
      <br/>

      <Pricing2
        numberOfCards={3}
        headings={["BASIC", "STANDARD", "PREMIUM"]}
        whichCardIsRecommended={1}
        prices={["$5 / sec", "$8 / sec", "$15 / sec"]}
        cardPointers={[
          [
            "Footage trimming and rearranging",
            "Color correction and grading",
            "Audio level adjustment and noise reduction",
            "Basic text titles and captions",
            "Simple transitions between clips",
            "Basic audio background music",
            "Expedited turnaround time",
            "Fast communication with clients"
          ],
          [
            "Advanced color correction and grading",
            "Multi-camera footage syncing and editing",
            "Audio mixing and sound design",
            "Motion graphics and visual effects",
            "Advanced text titles and captions",
            "Green screen keying and compositing",
            "Expedited turnaround time",
            "Fast communication with clients",
            "Flexible scheduling and extended working hours",
            "Dedicated team working on project"
          ],
          [
            "Script development and storyboarding",
            "Professional video filming with lighting and sound equipment",
            "Full-scale editing with advanced color grading and motion graphics",
            "Customized music and sound design",
            "Voiceover recording and integration",
            "Closed captioning and subtitles",
            "Expedited turnaround time",
            "Fast communication with clients",
            "Flexible scheduling and extended working hours",
            "Dedicated team working on project"
          ],
        ]}
      />

      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>

      <Footer/>
    </div>
  );
}
